import uniStatusBar from '@/components/uni-status-bar/uni-status-bar.vue'
import otherLogin from '@/components/home/other-login.vue'

export default {
	data() {
		return { 
			status: false, // false 账号密码登录 true 手机登录
			disabled: true,
			loading: false,
			username: '',
			pass: '',
			phone: '',
			code: '',
			codeTime: 0
		}
	},
	components: {
		uniStatusBar,
		otherLogin
	},
	methods: {
		// 获取验证码
		async getCode(){
			if(this.codetime > 0) return;
			// 验证手机号合法性
			if(!this.isPhone()){
				return uni.showToast({ title: '请输入正确的手机号码', icon:"none" });
			}
			// 请求服务器，发送验证码
			let [err,res] = await this.$http.post('/user/sendcode',{
				phone:this.phone
			});
			// 请求失败处理
			this.$http.errorCheck(err,res);
			if (res.data.errorCode === 30001) return;
			// 发送成功，开启倒计时
			this.codeTime=60;
			let timer=setInterval(()=>{
				this.codeTime--;
				if(this.codeTime < 1){
					clearInterval(timer);
					this.codeTime=0;
				}
			},1000);
		},
		// 初始化表单
		initInput () {
			this.username = '',
			this.pass = '',
			this.phone = '',
			this.code = ''
		},
		// 返回上一步
		back () {
			uni.navigateBack({ delta:1 })
		},
		// 登录
		submit () {
			// 账号密码登录
			if (!this.status) {
				return this.User.login({
						url:"/user/login",
						data:{
							username:this.username,
							password:this.pass
						}
					})
			} 
			// 验证码登录
			// 验证手机号合法性
			if(!this.isPhone()){
				return uni.showToast({ title: '请输入正确的手机号码', icon:"none" });
			}
			return this.User.login({
				url:"/user/phonelogin",
				data:{
					phone:this.phone,
					code:this.code
				}
			});
		},
		// 切换登录状态
		changeSatus () {
			this.status = !this.status
			this.initInput()
		},
		// 改变按钮状态
		changeBtn () {
			if ((this.username && this.pass) || (this.phone && this.code)) {
				this.disabled = false;
				return;
			}
			this.disabled = true
		},
		// 验证手机号码
		isPhone () {
			let pattern = /^1[34578]\d{9}$/;
			return pattern.test(this.phone)
		},
	},
	watch: {
		username (val) { this.changeBtn() },
		pass (val) { this.changeBtn() },
		phone (val) { this.changeBtn() },
		code (val) { this.changeBtn() }
	}
}
